.panel {
  display: flex;
  justify-content: center;

  &__count {
    padding: 0 0 5px 0;
  }

  &__items {
    background-color: lavender;
    margin: 0 5px 2px 0;
    display: flex;
    align-items: center;
    justify-content: center;

    &__text-content {
      margin: 0 2px;
    }
  }
}

.background_section {}

.button-space {
  margin: 5px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.active {
  font-weight: 900;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.curved-edges {
  border-radius: 16px;
}